<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li>Next</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <Container :Insta = "Insta" />
  <button @click="more" id="btn_more">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>
</template>

<script>
import Insta from './assets/Insta';
import Container from './components/Container';
import axios from 'axios';

export default {
  name: "App",
  data(){
    return {
      Insta : Insta,
      moreCnt : 0
    }
  },
  components: {
    Container : Container,
  },
  methods : {
    more(){
      // axios.post('https://codingapple1.github.io/vue/more0.json', { name : 'lee' }).then().catch(() => {

      // });

      if(this.moreCnt < 2){
        axios.get('https://codingapple1.github.io/vue/more' + this.moreCnt + '.json')
        .then( result => {
          this.Insta.push(result.data);

          this.moreCnt++;
        });

        if(this.moreCnt == 1){
          document.getElementById("btn_more").remove();
        }
      }
    }
  }
};
</script>

<style>
@import 'css/app.css';
</style>
